import { getSignUpUpdateValues } from "./PasswordStep.helpers"

const mockGeneratedUUID = "mock-generated-uuid"
jest.mock("uuid", () => ({
  v4: () => mockGeneratedUUID,
}))

describe("PasswordStep.helpers", () => {
  describe("getSignUpUpdateValues", () => {
    const values = { first: "first-123", last: "last-123", secret: "secret-123" }
    const unsafeMetadata = {
      self_registered: true as const,
      requireOnboarding: true as const,
      source: "web-client" as const,
      initialLogin: true as const,
      utm: {
        utm_source: "utm_source-123",
        utm_medium: "utm_medium-123",
        utm_campaign: "utm_campaign-123",
        utm_term: "utm_term-123",
        utm_content: "utm_content-123",
      },
    }
    const expectedReturn = {
      firstName: values.first,
      lastName: values.last,
      password: values.secret,
      legalAccepted: true,
      unsafeMetadata: {
        ...unsafeMetadata,
        initialLoginId: mockGeneratedUUID,
      },
    }

    it("should return the correct object", () => {
      expect(getSignUpUpdateValues(values, unsafeMetadata)).toStrictEqual(expectedReturn)
    })
  })
})
